.search-header {
    width: 100%;
    height: 2.5rem;
    background: #FFFFFF;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    -webkit-align-items: center;
    border-bottom: 1px solid #EFEFEF;
}

.search-header .back {
    width: 2rem;
    height: 2rem;
    background-image: url("../../../images/home/goods/back.png");
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: center;
}

.search-header .search {
    width: 80%;
    height: 1.5rem;
    border: solid 1px #B2B2B2;
    border-radius: 0.2rem;
    font-size: 0.6rem;
    color: #626262;
    line-height: 1.5rem;
    padding-left: 0.5rem;
}

.goods-main {
    width: 100%;
    height: 92.5vh;
    display: flex;
    display: -webkit-flex;
}

.goods-main .classify-wrap {
    width: 4.3rem;
    height: 100%;
    overflow: hidden;
    margin-right: 3%;
    position: relative;
    z-index: 1;
}

.goods-main .classify-wrap .classify-item {
    width: 100%;
    height: 2rem;
    border-bottom: 1px solid #EFEFEF;
    background-color: #FFFFFF;
    font-size: 0.7rem;
    text-align: center;
    line-height: 2rem;
    overflow: hidden;
}

.goods-main .classify-wrap .classify-item.active {
    color: #ff0000
}

.goods-main .goods-content {
    width: 71%;
    height: 100%;
}